/* app.css */

.container {
  flex: 1;
  padding: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.button {
  margin-bottom: 10px;
}

.list-item {
  padding: 10px;
  background-color: #f1f1f1;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.input {
  border-width: 1px;
  margin-bottom: 10px;
  padding: 5px;
  width: 100%;
}

.message-box {
  border-width: 1px;
  padding: 10px;
  margin-bottom: 10px;
}

.chat-item {
  padding: 10px;
  background-color: #f1f1f1;
  margin-top: 5px;
  margin-bottom: 5px;
}

.chat-title {
  margin-bottom: 10px;
}

/* Context Menu Styles */
.context-menu {
  position: absolute;
  background-color: white;
  border: 1px solid #ccc;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  width: 150px;
  padding: 5px;
  border-radius: 5px;
}

.context-menu-item {
  padding: 10px;
  cursor: pointer;
}

.context-menu-item:hover {
  background-color: #eee;
}
